import React from "react"
import { graphql, Link, useStaticQuery } from "gatsby"

import { rhythm, scale } from "../utils/typography"
import styled from "@emotion/styled"

export const SecondaryHeader = ({ title }) => (
  <h3
    style={{
      fontFamily: `Montserrat, sans-serif`,
      marginTop: 0,
    }}
  >
    <Link
      style={{
        boxShadow: `none`,
        textDecoration: `none`,
        color: `inherit`,
      }}
      to={`/`}
    >
      {title}
    </Link>
  </h3>
)

const Menu = styled.ul({
  listStyleType: 'none'
})

const MenuItem = styled.li({
  ...scale(1 / 4),
  display: 'inline-block',
  '&:after': {
    margin: rhythm(1 / 4),
    content: '"\\2014"'
  },
  '&:last-child:after': {
    content: '""'
  }
})

function MenuEntry({ to, current, children }) {
  return (
    <MenuItem><Link style={current === to ? {boxShadow: 'none'} : null} to={to}>{children}</Link></MenuItem>
  )
}

function Layout({ children, location }) {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
          author
        }
      }
    }
  `);
  const title = data.site.siteMetadata.title
  const current = location && location.pathname
  return (
    <div
      style={{
        marginLeft: `auto`,
        marginRight: `auto`,
        maxWidth: rhythm(24),
        padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
      }}
    >
      <header>
        <SecondaryHeader title={title}/>
        <Menu>
          <MenuEntry current={current} to="/experiences/">About</MenuEntry>
          <MenuEntry current={current} to="/projects/">Projects</MenuEntry>
          <MenuEntry current={current} to="/blog/">Blog</MenuEntry>
        </Menu>
      </header>
      <main>{children}</main>
      <footer>

      </footer>
    </div>
  )
}

export default Layout